@import '../base/variables.scss';

.slider{
    // border: 1px solid tomato;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.slide{
    // border: 1px solid blue;
    min-width: 100%;
    height: auto;
    transition: 0.5s;
    overflow: hidden;
}

%btn-styles {
    position: absolute;
    top: 43%;
    transform: translateY(-50%);
    width: 40%;
    height: 85%;
    background: none;
    // border: 1px solid green;
}

#prevSlide{
    left: 0;
    @extend %btn-styles;
    cursor: pointer;
}

#nextSlide{
    right: 0;
    @extend %btn-styles;
    // cursor: pointer;
}

#nextSlide:hover{
    cursor: url("../../images/arrow_right_alt.png"), pointer;
}

#prevSlide:hover{
    cursor: url("../../images/arrow_left_alt.png"), pointer;
}

.slide-pg-count{
    margin-top: $rel-margin;
}

.slide-container{
    position: relative;
}